import React, { FunctionComponent, useState } from 'react';
import styled from '@emotion/styled';
import Link, { LinkComponent } from 'components/atoms/Link';
import Text, { TextComponent } from 'components/atoms/Text';
import storage from 'utilities/localStorage';

interface NavBarProps {
  modeSwitch: boolean;
}

const MODE_STORAGE_KEY = 'blog-current-mode';

export const NavBarComponent = styled.div`
  display: flex;
  font-weight: 100;

  ${TextComponent} {
    cursor: pointer;
    font-size: 15px;
  }

  ${LinkComponent} + ${TextComponent},
  ${LinkComponent} + ${LinkComponent} {
    margin-left: 20px;
  }
`;

const ModeSelectSwitch = styled(Text)``;

const NavBar: FunctionComponent<NavBarProps> = function ({ modeSwitch }) {
  const [currentMode, setCurrentMode] = useState<string>(storage.get(MODE_STORAGE_KEY));

  const changeMode = () => {
    window.document.body.classList.toggle('dark');
    storage.set(MODE_STORAGE_KEY, storage.get(MODE_STORAGE_KEY) === 'light' ? 'dark' : 'light');
    setCurrentMode(currentMode === 'light' ? 'dark' : 'light')
  };

  return (
    <NavBarComponent>
      <Link to="/">
        <Text>Main</Text>
      </Link>
      <Link to="/about">
        <Text>About</Text>
      </Link>
      <Link to="/portfolio">
        <Text>Portfolio</Text>
      </Link>
      <Link to="/blog/1">
        <Text>Blog</Text>
      </Link>
      {modeSwitch && (
        <ModeSelectSwitch onClick={changeMode}>
          {currentMode === 'light' ? 'Dark' : 'Light'} Mode
        </ModeSelectSwitch>
      )}
    </NavBarComponent>
  );
};

export default NavBar;
